import React, { Component } from "react";
import { Row, Col, Container } from "react-bootstrap";
import { Hasil, DaftarKategori, NavbarComponent, Menus } from "./component/Index";
import { API_URL } from "./utils/constants";
import axios from "axios";

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menus: [],
      pilihKategori : 'Lauk Pauk'
    }
  }

  componentDidMount() {
    axios
      .get(API_URL + "products?category.nama="+ this.state.pilihKategori)
      .then((res) => {
        const menus = res.data;
        this.setState({ menus });
      })
      .catch((error) => {
        console.log("Error yaa", error);
      });
  }

  changeCategory = (value) => {
    this.setState({
      pilihKategori : value,
      menus: []
    })
    axios
    .get(API_URL + "products?category.nama="+ value)
    .then((res) => {
      const menus = res.data;
      this.setState({ menus });
    })
    .catch((error) => {
      console.log("Error yaa", error);
    });

  }
  render() {
    const{menus, pilihKategori} = this.state
    return (
      <div>
        <div className="App">
          <NavbarComponent />
          <div className="mt-3">
            <Container fluid>
              <Row>
                <DaftarKategori changeCategory={this.changeCategory} pilihKategori={pilihKategori} />
                <Col>
                  <h4>
                    <strong>Produk Kami</strong>
                  </h4>
                  <hr />
                  <Row>
                    {menus && menus.map((menu)=>(
                      <Menus
                        key={menu.id}
                        menu = {menu}
                      />
                    ))}
                  </Row>
                </Col>
                <Hasil />
              </Row>
            </Container>
          </div>
        </div>
      </div>
    );
  }
}
